.galary {
    font-size: 14px;
    line-height: 24px;
}

.galary__item__big-road {
    width: 100%;
    height: auto;
}


.galary__img-about {
    padding: 14px 0 14px;
}

.galary__title {
    text-align: center;
    line-height: 26px;
    font-size: 18px;
    margin: 0 0 25px;
}

.galary__promo {
    text-align: center;
    margin: 0;
}

.galary-greeting {
    margin-bottom: 40px;
}

.galary__sub-list__img {
    width: 100%;
    height: auto;
}

.galary__description {
    border-bottom: 1px solid #e5e5e5;
    width: 280px;
    margin: 0 auto 19px;
    padding-bottom: 30px;
}

.galary__inner {
    padding: 0 20px;
    margin-bottom: 36px;

}

.galary-greeting__text {
    width: 229px;
    height: 39px;
    margin-top: -21px;
}

.galary__sub-list {
    margin: 0;
    list-style: none;
    padding: 0;
}

.galary__item-info {
    margin: 0;
}

.galary__images {
    padding: 0;
    margin: 0;
    list-style: none;
}

.galary-greeting__img {
    min-height: 154px;
}

.galary-greeting__wrapper {
    display: none;
    padding: 30px;
}

.galary__item {
    border-bottom: 1px solid #e5e5e5;
    margin-bottom: 25px;
}


.galary__item-description {
    font-weight: bold;
}

.galary__item-author {
    text-transform: none;
}

.galary__item-likes {
    padding: 6px 0 0;
}

.galary__item-likes svg {
    margin-right: 10px;
}




.video__player--inner {
    min-height: 65px;
    background-color: #f2f2f2;
    width: 280px;
    margin: 0 auto;
}

@media (min-width: 768px) {
    .galary {
        padding: 0;
        margin-bottom: 60px;
    }

    .galary__description {
        margin-bottom: 39px;
    }

    .galary__inner {
        padding: 0 60px;
    }

    .galary__item {
        border-bottom: none;
        margin-bottom: 0;
    }

    .galary-greeting__text {
        width: 458px;
        height: 78px;
    }

    .galary-greeting__img {
        min-height: 287px;
        margin-bottom: 76px;
    }

    .galary__img-about {
        background-color: #f2f2f2;
        display: flex;
        justify-content: space-between;
        padding: 16px;
    }

    .galary__sub-list .galary__item {
        width: 50%;
    }

    .galary__sub-list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .galary__sub-list .galary__item:nth-child(2) .galary__img-about {
        background-color: #eaeaea;
    }

    .galary__item__big-road {
        width: 100%;
        height: auto;
    }

    .galary__title {
        font-size: 30px;
        margin: 0 0 31px;
    }

    .galary__description {
        width: 432px;
        border-bottom: unset;
    }


}

@media (min-width: 1230px) {
    .galary-greeting__img {
        margin-bottom: 126px;
    }

    .galary {
        margin-bottom: 72px;
    }

    .galary__inner {
        padding: 0;
    }

    .galary__item-likes svg {
        fill: #e5e5e5;
    }

    .galary__sub-list .galary__item {
        width: 33.3%;

    }

    .galary__item-info {
        padding: 0;
        position: relative;

    }

    .galary__img-about {
        width: 100%;
        position: absolute;
        bottom: 0;
        color: #ffffff;
        background: rgba(0, 0, 0, .3);
    }

    .galary__sub-list .galary__item:nth-child(2) .galary__img-about {
        background: rgba(0, 0, 0, .5);
    }
}