.advantages__list {
    padding: 0;
    width: 280px;
    list-style: none;
    display: flex;
    flex-direction: column;
    margin: 0 auto 23px;
}

.advantages__list-item__icon {
    margin-right: 12px;
}

.advantages {
    position: relative;
}

.advantages__number {
    display: inline-block;
    margin-bottom: 19px;
}

.advantages__list-item__text-heading {
    margin: 0;
}

.advantages__description {
    margin: 0;
}

.advantages__icon {
    width: 38px;
    height: 40px;
    fill: #81b3d2;
}

.advantages__promo {
    margin-bottom: 24px;
}


.advantages__promo__img-photo {
    width: 100%;
    object-fit: cover;
}


.advantages__bottom-list__text {
    padding-bottom: 20px;
    margin: 0;
    padding: 0;
}

.advantages__promo-info {
    background-color: #81b3d2;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #ffffff;
    font-size: 14px;
    line-height: 21px;
    padding: 63px 28px 39px 27px;
    text-align: center;
}

.advantages__bottom-promo .advantages__promo-info {
    padding-top: 41px;
}

.advantages__promo__title {
    font-weight: bold;
    font-size: 18px;
    margin: 0 0 17px;
}

.advantages__list-item__text-heading {
    font-size: 18px;
    font-weight: 700;
    padding: 12px 0 0;
    margin-bottom: 16px;
}

.advantages__list-item:nth-child(2) {
    order: 1;
}

.advantages__list-item {
    display: flex;
    margin-bottom: 17px;
}

.advantages__list-icon {
    margin-right: 12px;
}

.bottom-list__item {
    padding: 26px 21px 28px;
    text-align: center;
    border-bottom: 1px solid #d6d6d6;
    line-height: 21px;
    font-size: 14px;
}

.bottom-list__tourists {
    padding-left: 14px;
    padding-right: 14px;
}

.advantages__list-item__text-info {
    font-size: 14px;
    line-height: 21px;
    margin: 0;
}

.advantages__bottom-list__wrapper {
    background-color: #eeeeee;
}

@media (min-width: 768px) {
    .advantages__promo {
        display: flex;
        margin-bottom: 37px;
    }

    .advantages__description {
        padding: 0 20px;
    }

    .advantages__bridge-text {
        padding: 0 20px;
    }

    .advantages__promo__img {
        width: 66.6%;
    }

    .advantages::before {
        display: none;
    }

    .bottom-list {
        margin: 0;
    }

    .advantages__list-item:nth-child(2) {
        order: initial;
    }

    .bottom-list__item {
        width: 33.3%;
        padding: 44px 49px 54px;
        border-bottom: unset;
    }

    .advantages__icon {
        width: 76px;
        height: 76px;
        margin-bottom: 19px;
    }

    .advantages__list-item__text-heading {
        padding: 0;
        margin-bottom: 23px;
        font-size: 21px;
        line-height: 26px;
    }

    .advantages__promo__title {
        font-size: 21px;
    }

    .advantages {
        margin-bottom: 50px;
    }

    .advantages__promo-info {
        justify-content: center;
        align-items: center;
        padding: 41px 24px 39px 27px;
        width: 33.3%;
    }

    .advantages__list {
        width: auto;
        flex-direction: row;
        flex-wrap: wrap;
        margin: 0 0 23px;
        align-items: center;
    }

    .advantages__list-item {
        flex-direction: column;
        align-items: center;
        text-align: center;
        width: 33.3%;
        padding: 20px 45px;
    }

    .advantages__bottom-list__text {
        border-bottom: none;
    }

    .advantages__bottom-promo {
        flex-direction: row-reverse;
        margin-bottom: 0;
        display: flex;
    }

}

@media (min-width: 1250px) {
    .advantages__promo-info {
        width: 400px;
        padding: 25px 50px 39px 51px;
    }

    .advantages__list-item:nth-child(2) {
        padding: 20px 59px;
    }

    .advantages__list {
        margin: 0 0 42px;
    }

    .advantages__promo__title {
        line-height: 26px;
        margin-bottom: 12px;
        padding: 0 78px;
    }

    .advantages__list-item {
        width: 400px;
        padding: 20px 70px;
    }

    .advantages__devil-bridge {
        padding: 25px 64px 39px 64px;
    }

    .bottom-list__item {
        width: 400px;
        padding: 43px 47px 66px 48px;
    }

}