.landmarks__cell {
    min-height: 60px;
    font-size: 18px;
    line-height: 26px;
    font-weight: bold;
    text-transform: none;
    margin-bottom: 20px;
}

.landmarks__title {
    font-weight: bold;
    text-align: center;
    font-size: 18px;
    line-height: 26px;
    margin-bottom: 16px;
}

.landmarks {
    margin-bottom: 21px;
}

.landmarks__label {
    display: flex;
    align-items: center;
    position: relative;
}

.landmarks__checkbox:focus+.landmarks__label::before {
    background-color: #fff;
    box-shadow: inset 0 0 0 4px #e5e5e5;
}


.landmarks__checkbox:active+.landmarks__label::after {
    opacity: .3;
}

.landmarks__checkbox+.landmarks__label::before {
    position: relative;
    content: "";
    width: 55px;
    height: 55px;
    background-color: #f2f2f2;
    border-radius: 8px;
    margin-right: 22px;
}


.landmarks__checkbox:checked+.landmarks__label::after {
    content: "";
    background-image: url(../img/icon-tick.svg);
    background-repeat: no-repeat;
    position: absolute;
    top: 18px;
    left: 15px;
    width: 25px;
    height: 17px;
}

@media (min-width: 768px) {
    .landmarks__wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    .landmarks {
        margin-bottom: 40px;
    }

    .landmarks__cell {
        width: 229px;
        margin-bottom: 28px;
        margin-left: 30px;
    }

    .landmarks__title {
        padding: 0 160px;
        margin-bottom: 38px;
        font-size: 21px;
    }


}

@media (min-width: 1230px) {
    .landmarks__title {
        font-size: 21px;
        padding: 0 274px;
    }

    .landmarks__cell {
        width: 205px;
        margin-left: 0;
        margin-right: 0px;
    }

    .landmarks__wrapper {
        justify-content: space-between;
    }
}