body {
  font-family: "PT Sans", "Arial", sans-serif;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  box-sizing: border-box;
  background-color: #f2f2f2
}

.shadow-wrapper {
  box-shadow: 0 5px 15px rgba(0, 1, 1, .2);
  background-color: #fff;
  max-width: 1200px;
  margin: 0 auto;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  // outline: 1px solid lightgreen;
}

textarea:active, textarea:focus { outline: none; }


.show-content {
  display: block;
}

img {
  display: block;
}


.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}