.review__img-text {
    width: 229px;
    height: 40px;
    margin-top: -21px;
}

.review__img {
    min-height: 154px;
    margin-bottom: 40px;
}

.review-wrapper__title {
    margin-bottom: 23px;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    line-height: 26px;
    font-size: 18px;
    margin: 0 0 25px;
}

.review-wrapper {
    margin-bottom: 21px;
    border-bottom: 1px solid #e5e5e5;
}

.review-wrapper__about {
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    color: #333333;

}


@media (min-width: 768px) {
    .review__img {
        min-height: 282px;
    }

    .video__player-inner__subtitles {
        margin-left: auto;
    }

    .review {
        margin-bottom: 82px;
    }

    .review-wrapper__title {
        margin-bottom: 32px;
        font-size: 30px;
    }

    .review-wrapper__about {
        padding: 0 144px;
    }

    .review-wrapper {
        margin-bottom: 67px;
    }

    .review__img-text {
        width: 463px;
        height: 77px;
        margin-top: -16px;
    }

}

@media (min-width: 1230px) {
    .review {
        margin-bottom: 130px;
    }

    .review-wrapper__about {
        padding: 0 368px;
    }

    .review-wrapper {
        margin-bottom: 53px;
    }
}