.welcome {
    line-height: 26px;
    margin-bottom: 20px;
}

.welcome__details {
    position: relative;
    font-size: 14px;
    padding: 0 35px;
    margin: 0;
    text-align: center;
}

.decorative-triangle {
    position: relative;
    background: url("../img/bg-back-mobile@1x.jpg") no-repeat bottom center/cover;
    width: auto;
    min-height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;

    @media (min-resolution: 1.5dppx) {
        background: url("../img/bg-back-mobile@2x.jpg") no-repeat bottom center/cover;
    }
}

.decorative-triangle::after {
    content: "";
    width: 100%;
    position: absolute;
    min-height: 57px;
    bottom: 0;
    background: url("../img/bg-intro-triangle-mobile.svg");
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: 100%;
}

.welcome__text-img {
    width: 282px;
}

.welcome__img {
    min-height: 386px;
    margin-bottom: 41px;
}

.welcome__inner-details {
    position: relative;
}


.welcome__title {
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    margin: 0;
    margin: 0 0 27px;
    padding: 0 30px;
}

@media (min-width: 768px) {
    .welcome__title {
        font-size: 21px;
    }

    .welcome__img::before {
        bottom: -1px;
    }

    .welcome__text-img {
        width: 460px;
    }

    .welcome__img {
        min-height: 517px;
        margin-bottom: 49px;
    }

    .welcome__details {
        text-align: center;
        padding: 0;
    }

    .welcome__inner-details {
        margin-bottom: 50px;
        padding: 0 127px;

        &::before,
        &::after {
            display: none;
        }
    }

    .decorative-triangle::after {
        bottom: -1px;
        background-size: 100% 57px;
        background: url(../img/bg-intro-triangle-tablet.svg) no-repeat center;
    }
}

@media (min-width: 1230px) {
    .welcome__inner-details {
        padding: 0 287px;
    }

    .welcome__img {
        background-position: 50%;
    }

    .decorative-triangle::after {
        background: url(../img/bg-intro-triangle-desktop.svg) no-repeat center;
        
    }
}