.contacts__info {
    font-size: 14px;
    line-height: 26px;
    text-transform: uppercase;
}

.contacts__input {
    font-size: 18px;
    line-height: 26px;
    padding: 15px 0 15px 19px;
};
    // padding: 15px 0 15px 10px;


.contacts__label-desktop {
    display: none;
}

.contacts__title {
    width: 100%;
    margin-bottom: 18px;
    font-weight: bold;
    text-align: center;
    font-size: 18px;
    line-height: 26px;
}

.contacts {
    order: -1;
    margin-bottom: 33px;
}

.contacts__info {
    position: relative;
    text-transform: uppercase;
    display: inline-block;
}

.contacts__label {
    padding-left: 24px;
    margin-bottom: 7px;
    display: inline-block;
}


.contacts__info::after {
    content: "";
    width: 58px;
    background-color: #e6e6e6;
    position: absolute;
    right: 0;
    background-repeat: no-repeat;
    background-position: center;
    height: 56px;
}

.contacts-phone::after {
    background-image: url("./../img/icon-phone.svg");
}

.contacts-email::after {
    background-image: url("./../img/icon-mail.svg");
}

.contacts__cell {
    margin-bottom: 13px;
}

@media (min-width: 768px) {
    .contacts {
        width: 100%;
    }

    .contacts__info {
        width: 100%;
    }

    .contacts__title {
        position: relative;
        margin-bottom: 22px;
        font-size: 21px;
    }

    .contacts__cell {
        margin-bottom: 22px;
    }

    .contacts__title::before {
        content: "";
        position: absolute;
    }


}


@media (min-width: 1230px) {
    .contacts__cell-wrapper {
        display: flex;
        justify-content: space-between;
        width: 960px;
    }

    .contacts__label--phone {
        display: none;
    }

    .contacts__input {
        width: 314px;
    }

    .contacts__title {
        margin-bottom: 70px;
    }

    .contacts__label {
        width: 163px;
        padding-left: 0;
    }

    .contacts__info {
        display: flex;
        align-items: center;

    }

    .contacts-phone {
        width: 400px;
    }

    .contacts__label-desktop {
        display: block;
        width: 83px;
    }


}