.introduction {
    width: 100%;
    order: -2;
    margin-bottom: 27px;
}

.introduction__title {
    font-size: 18px;
    line-height: 26px;
    font-weight: bold;
    text-align: center;
    margin: 0 0 21px;
}

.introduction__field {
    margin-bottom: 20px;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.introduction__name {
    margin-right: 20px;
}


@media (min-width: 768px) {
    .introduction {
        width: 290px;
        order: -1;
    }

    .introduction__field {
        padding-left: 27px;
    }

    .introduction__title {
        margin-bottom: 56px;
        font-size: 21px;
        font-size: 21px;
        text-align: left;
        padding: 6px 58px 0 25px;
    }

}

@media (min-width: 1230px) {
    .introduction__title {
        font-size: 21px;
        padding-left: 0;
    }

    .introduction {
        width: 400px;
    }

    .introduction__field {
        padding-left: 0;
    }

}