.reservation-form {
    width: 280px;
    margin: 0 auto;
    position: absolute;
    z-index: 2;
    background-color: #ffff;
    left: 50%;
    margin-left: -140px;
    padding: 20px 20px;
    box-shadow: 0 5px 15px rgba(0, 1, 1, .6);
}

.reservation-form label {
    margin-bottom: 20px;
    font-weight: bold;
}

.field-group {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
}

.input-btn {
    position: relative;
    width: 25px;
    height: 25px;
    border: unset;
}

.input-btn::before {
    content: "";
    position: absolute;
    width: 10px;
    height: 2px;
    background-color: #333333;
    left: 6px;
}

.plus-btn::after {
    content: "";
    position: absolute;
    width: 10px;
    height: 2px;
    background-color: #333333;
    left: 6px;
    transform: rotate(-90deg);
}

.input-number {
    width: 25px;
    height: 25px;
    border: none;
    text-align: center;
}

.form-row__amount {
    display: flex;
}

.form-row__small-field {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.field-group input {
    font-size: 12px;
    font-family: inherit;
    color: #333333;
}

.form-row__btn {
    display: inline-block;
    vertical-align: middle;
    border: none;
    background: #f2f2f2;
    width: 38px;
    height: 38px;
    cursor: pointer;
    padding: 9px;
}

.field-group input {
    background: #f2f2f2;
    font-weight: 700;
    text-transform: uppercase;
    border: none;
    padding: 11px;
    width: 100%;
    margin-left: auto;
}

.field-group {
    display: flex;
    flex-direction: column;
    width: 100%;
}

@media (min-width: 768px) {
    .reservation-form {
        width: 510px;
        margin: 0 auto 0 -255px;

        label {
            margin-right: 20px;
        }
    }

    .small-row {
        display: flex;
        justify-content: space-between;
        padding: 0 20px;
    }

    .reservation__button {
        width: 100%;
    }
}