.video__extra-info {
    padding: 0 40px;
    margin-bottom: 35px;
}

.video__player {
    background-color: transparent;
    border: none;
}

.video__player-inner__subtitles {
    margin-right: 31px;
}

.video__about {
    font-size: 14px;
    line-height: 24px;
    text-align: center;
}

.video {
    margin-bottom: 75px;
}

.video__title {
    font-size: 18px;
    line-height: 26px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 26px;
}

.video__player {
    position: relative;
}

.video__player-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 23px;
    min-height: 66px;
    width: 280px;
    margin: 0 auto;
    background-color: #f2f2f2;
}



.video__slider {
    position: absolute;
    width: 280px;
    height: 45px;
    background: rgba(0, 0, 0, 0.5);
    bottom: 0;
    margin-left: 50%;
    left: -140px;
}

.video__range {
    height: 3px;
    width: 235px;
    background: rgba(255, 255, 255, 0.3);
    top: 21px;
    position: absolute;
    left: 19px;
}

.video__volume {
    width: 10px;
    height: 10px;
    background-color: #fff;
    border-radius: 50%;
    position: absolute;
    bottom: -4px;
    left: 40px;
}

.video__player-preview {
    width: 100%;
    height: auto;
}

@media (min-width: 768px) {
    .video__extra-info {
        width: 418px;
        margin: 0 auto;
        margin-bottom: 75px;
        padding: 0;
    }

    .video__slider {
        width: 650px;
        left: -325px;
        height: 90px;
    }

    .video__range {
        width: 394px;
        left: 90px;
        top: 40px;
    }

    .video__title {
        margin-bottom: 30px;
        font-size: 30px;
        text-align: center;
    }

    .video__player-preview {
        margin: 0 auto;
        width: 648px;
    }

    .video__player-inner {
        min-height: 95px;
        position: sticky;
        margin-top: -90px;
        width: 648px;
        background-color: transparent;
    }

    .video__player-inner svg path {
        fill: white;
    }
}

@media (min-width: 1230px) {
    .video__extra-info {
        padding: 0;
    }
    .video__volume{
        left: 114px;
    }

    .video__range {
        width: 543px;
        top: 37px;
    }
    .video__player-inner__subtitles{
        margin-right: 27px;
    }

    .video__player-inner {
        width: 800px;
        margin-top: -98px;
        padding: 0 37px 0 28px;
    }

    .video__subtitles {
        margin-right: -8px;
    }

    .video__player-preview {
        width: 800px;
    }

    .video__slider {
        width: 800px;
        left: -400px;
    }

    .video {
        margin-bottom: 93px;
    }
}