.information__title {
    font-size: 21px;
    line-height: 26px;
    font-weight: bold;
    margin-bottom: 20px;
}

#information__map {
    width: 100%;
    height: 350px;
    margin-bottom: 34px;
}

.information__btn {
    position: relative;
}


.information__details {
    margin: 0 0 33px;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
}

.main-style-btn {
    background-color: #81b3d2;
    font-family: inherit;
    color: #ffffff;
    font-size: 21px;
    line-height: 26px;
    text-transform: uppercase;
    font-weight: bold;
    width: 100%;
    padding: 18px 20px 15px;
    border-style: none;
}

.main-style-btn:hover {
    background-color: #669ec0;
}

.main-style-btn:active {
    background-color: #5496bd;
    color: rgba(255, 255, 255, 0.3);
}

.information__wrapper {
    text-align: center;
    padding: 0 20px 50px;
}


@media (min-width: 768px) {
    .information__title {
        font-size: 30px;
        margin-bottom: 31px;
    }
    .information__details {
        padding: 0 16px;
        margin: 0 0 39px;
    }

    .information__map {
        margin-bottom: 0;
    }

    .information__wrapper {
        width: 460px;
        margin: 0 auto;
        padding: 0;
        margin-bottom: 57px;
    }
}

