.popUp {
    font-size: 18px;
    width: 320px;
    background-color: #fff;
    line-height: 26px;
    text-align: center;
    padding: 40px;
    box-shadow: 0 1px 5px 0 rgba(0,0,0,.75);
    position: absolute;
    top: 70%;
}

.popup__header {
    font-weight: bold;
    margin: 0;
    margin-bottom: 20px;
}

.popup__text {
    display: inline-block;
    text-align: center;
    margin: 0 0 40px;
    font-size: 14px;
    margin-bottom: 20px;
}


