.form__title {
    padding-left: 18px;
    font-size: 18px;
    line-height: 26px;
    font-weight: bold;
    margin: 0;
    margin-bottom: 21px;
}

.field-gray:focus{
    background-color: #fff;
    box-shadow: inset 0 0 0 4px #ccc;
    outline: none;
}

.form__input{
    font-size: 18px;
    line-height: 26px;
}

.form__btn__required-field {
    color: transparent;
}

.form fieldset {
    border: 0;
    padding: 0;
}

.form__wrapper {
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    width: 280px;
}

.form__input:hover {
    background-color: #e5e5e5;
}

.text {
    margin-right: 14px;
}

.form__review {
    margin-bottom: 35px;

}

.form__review-checkbox {
    font-size: 18px;
    min-height: 60px;
    margin-bottom: 9px;
}

.form__review-checkbox__label {
    position: relative;
    display: flex;
    align-items: center;
    text-transform: initial;
}
.form__review-checkbox__label:focus{
    background-color: red;
}

.form__review-checkbox__label::before {
    content: "";
    left: 0;
    width: 57px;
    height: 57px;
    border-radius: 50%;
    background-color: #f2f2f2;
    margin-right: 20px;
    &:focus{
        box-shadow: 1px solid red;
    }
}


.form__review-checkbox__label:hover::before {
    background-color: #e5e5e5;
}

.form__review-checkbox__label:active::before {
    background-color: #d5d5d5;
}


.form__review-checkbox__radio:checked+.form__review-checkbox__label::after {
    position: absolute;
    content: "";
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background-color: #333333;
    left: 18px;
}

.field-gray {
    border-style: none;
    width: 100%;
    background-color: #f2f2f2;
    padding: 16px 20px 16px 19px;;
}

.field-gray::placeholder {
    font-size: 16px;
    line-height: 26px;
    font-weight: bold;
    color: #000;
    font-family: inherit;
}

@media (min-width: 768px) {
    .form__btn {
        max-width: 354px;
    }

    .form__wrapper {
        flex-direction: row;
        justify-content: space-between;
        width: 648px;
        padding: 0;
    }

    .form__introduction--inner {
        display: flex;
        justify-content: space-around;
        flex-direction: row-reverse;
        margin-bottom: 63px;
    }

    .form__review {
        width: 48%;
        order: -2;
        margin-bottom: 53px;
        width: 280px;
    }

    .form__btn__required-field {
        color: #000;
        font-size: 14px;
    }

    .form__title {
        font-size: 21px;
        padding: 6px 8px 0 0px;
        margin-bottom: 59px;
    }

    .form__review-checkbox {
        margin-bottom: 17px;
    }
}

@media (min-width: 1230px) {
    .form__title {
        font-size: 21px;
        text-align: center;
    }

    .field-gray::placeholder {
        font-size: 18px;
        line-height: 26px;
        vertical-align: middle;
    }

    .form__input {
        font-size: 21px;
        line-height: 26px;
        padding-top: 14px;
        padding-bottom: 17px;
    }

    .form__btn {
        max-width: 479px;
    }

    .form__wrapper {
        width: 960px;
    }

    .form__review {
        padding-left: 74px;
        width: 400px;
    }

}