.footer__inner {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    padding-bottom: 29px;
}


footer a {
    text-decoration: none;
    color: #000000;
}

.footer__developed {
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 130px;
}

.footer__developed__text {
    font-style: 14px;
    line-height: 26px;
    margin-right: 10px;
}

.footer__developed svg:hover {
    fill: #669ec0;
}

.footer__developed svg:active {
    fill: #a8a8a8;
}

.footer__social {
    list-style: none;
    display: flex;
    width: 153px;
    justify-content: space-between;
    margin: 0;
    padding: 0;
    margin: 0 0 40px;
}

.footer__social-img path {
    fill: white;
}

.footer__social-item {
    width: 48px;
    height: 48px;
    background-color: #81b3d2;
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer__social-item:hover {
    background-color: #669ec0;
}

.footer__social-item:active {
    background-color: #5496bd;
}

.footer__social-img:active path {
    fill: rgba(255, 255, 255, 0.3);
}


.footer__main-link {
    font-size: 21px;
    font-weight: bold;
    margin-bottom: 36px;
}

@media (min-width: 688px) {
    .footer__inner {
        flex-direction: row;
        justify-content: space-between;
        padding: 25px 60px;
    }

    .footer__social {
        margin: 0;
    }

    .footer__main-link {
        margin-bottom: 0;
    }
}

@media(min-width:1230px) {
    .footer__inner {
        padding: 27px 121px;
    }

}