.feedback__input {
    width: 100%;
    margin-bottom: 46px;
    height: 159px;
    resize: none;
    font-family: inherit;
    font-size: 18px;
    line-height: 26px;
}


.feedback__title {
    font-weight: bold;
    text-align: center;
    font-size: 18px;
    line-height: 26px;
    margin-bottom: 18px;
    width: 100%;
}

.feedback__input::placeholder {
    color: #333333;
    font-style: inherit;
    font-size: 18px;
    line-height: 26px;
    font-weight: bold;
}

.feedback__input:focus {
    background-color: #fff;
    box-shadow: inset 0 0 0 4px #ccc;
}

.feedback {
    margin: 0 auto;
}

.feedback {
    margin: 0 auto 52px;
    width: 280px;
}

@media (min-width: 768px) {
    .feedback__btn-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .feedback {
        width: 100%;
        margin-bottom: 70px;
    }

    .feedback__input {
        font-size: 18px;
        line-height: 26px;
    }

    .feedback__title {
        margin-bottom: 52px;
        font-size: 21px;
    }
}

